import { Component, OnInit } from '@angular/core';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';
import {LayerService} from '../../services/layer/layer.service';
import {GroupService} from '../../services/group/group.service';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.page.html',
  styleUrls: ['./add-item.page.scss'],
})
export class AddItemPage implements OnInit {
    
    public track:any;
    public layers:Array<any>;
    public selectedGroup:any;
    constructor(public modalController: ModalController, private layerService:LayerService, private groupService:GroupService, private navParams:NavParams) { 
    
    
        this.track = {};
        //let track = this.navParams.get('track');
        //this.track = Object.assign({}, track );   
        
        this.layers = [];  
        
        
        this.groupService.getCurrentGroup().then((data:any) => {

            let selectedGroup = data ? data : {};     
            this.selectedGroup = selectedGroup;

            if (selectedGroup.id){
                this.layerService.getGroupLayers(selectedGroup.id).then((layers:Array<any>) => {
                    this.layers = layers;
                })
            }
            else{
                this.layerService.getPrivateLayers().then((layers:Array<any>) => {
                    this.layers = layers;
                })                
            }


        })          
        
         
    
    
    }

    ngOnInit() {
    }
    
    
    public add(){
        if (this.track.layer_name){
            this.track.group_id = this.selectedGroup.id ? this.selectedGroup.id : "private";
        }       
        
           
    
        this.modalController.dismiss({track:this.track});  
    }
    
    
    public dismiss(){
        this.modalController.dismiss();
    }     

}
