import { Component, OnInit } from '@angular/core';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';
import {GroupService} from '../../services/group/group.service';


@Component({
  selector: 'app-invite-user',
  templateUrl: './invite-user.page.html',
  styleUrls: ['./invite-user.page.scss'],
})
export class InviteUserPage implements OnInit {
    public user:any;
    public groups:Array<any>;
    constructor(public modalController: ModalController, private groupService:GroupService) { 
    
        this.user = {};
      
        this.groups = [];
      
        this.groupService.getJoinedGroups().then((data:Array<any>) => {
            this.groups = data;
        });     
    
    }

    ngOnInit() {
    }
    
    public dismiss(){
        this.modalController.dismiss();
    }  
    
    
    public invite(){
        this.modalController.dismiss({user:this.user});
    }          
    

}
