import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams, AlertController } from '@ionic/angular';
import {GroupService} from '../../services/group/group.service';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {JoinGroupPage} from '../join-group/join-group.page';
import {EditGroupPage} from '../edit-group/edit-group.page';
import {ProfilePage} from '../profile/profile.page';
import {GroupInvitesPage} from '../group-invites/group-invites.page';

@Component({
  selector: 'app-groups',
  templateUrl: './groups.page.html',
  styleUrls: ['./groups.page.scss'],
})
export class GroupsPage implements OnInit {

    public user:any;
    public loading:boolean;
    public groups:Array<any>;
    public selectedGroup:any;
    public layers:Array<any>;
    public messages: Array<any>;
    public message:any;
    public messageInterval:any;
    public activeTab:string;



    constructor(private groupService:GroupService,public auth: AuthenticationService, public modalController: ModalController,private navParams: NavParams,
        private alertController:AlertController) { 
    
        this.user = {};
        this.auth.retreiveUserData().then((data) => {
            console.log(data);
            this.user = data;
        });  
        
        this.loading = true;
        this.groups = [];
        this.selectedGroup = {};
        
        this.groupService.getCurrentGroup().then((data:string) => {
            if (data){
                console.log(data);
                this.selectedGroup = data;  
            }
            
        })        
    
        this.getGroups();

        
        this.messages = [];
        this.message = {};     
        
        this.activeTab = "layers";
    
    }

    ngOnInit() {
    }
    
    ngOnDestroy(): void {
        if (this.messageInterval){
            clearInterval(this.messageInterval);
        }
    } 
    
    public groupNavigationChanged(tab){
        console.log(tab);
        this.activeTab = tab;
    }
    
    
    public createMessage(){
        
        if (!this.message.message){
            return;
        }
        
        let message:any = {};
        Object.assign(message,this.message);       
        message.group_id = this.selectedGroup.id;
        message.user_id = this.user.id;
        this.messages.push(message);
        this.message.message = "";
        
        this.groupService.createMessage(message).then((data) => {
            this.message = {};
        })
        
        
    }
    
    
    public setMessageInterval(){
        if (this.messageInterval){
            clearInterval(this.messageInterval);
        }
        
        this.messages = [];
        this.getMessages();
        this.messageInterval = setInterval(() => {
            this.getMessages();
        },10000);
                
    }
    
    public getMessages(){
        
        if (!this.selectedGroup || !this.selectedGroup.id){
            return;
        }
        
        this.groupService.getMessages(this.selectedGroup.id).then((data:Array<any>) => {
            this.messages = data;
        })
    }
    
    
    public getGroups(){
        this.groupService.getJoinedGroups().then((data:Array<any>) => {
            this.groups = data;
            if (!this.selectedGroup.id && this.groups.length > 0){
                //this.selectedGroup = this.groups[0]; 
            } 
            else if (this.selectedGroup.id){
                this.setMessageInterval();
            }
            
            this.loading = false;
        }).catch(() => {
            this.loading = false;
        })
    }
    
    public selectPrivate(){
        this.selectedGroup = {};
        this.groupService.setCurrentGroup({});   
        if (this.messageInterval){
            clearInterval(this.messageInterval);
        }
    }    
    
    
    public selectGroup(group){
        this.activeTab = "layers";
        this.selectedGroup = group;
        this.groupService.setCurrentGroup(group);   
        this.setMessageInterval();
    }
    
    
    public async joinGroup(){
        const modal = await this.modalController.create({
            component: JoinGroupPage,
            componentProps:{}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {

                if (result && result.data){     
                    this.groupService.getJoinedGroups().then((data:any) => {
                        this.groups = data;

                        for (let group of this.groups){
                            if (group.id === result.data.group_id){
                                this.selectedGroup = group;
                                break;
                            }
                        }

                    })                
                }
            });        
        
        
        
        await modal.present();           
    }
    
    public async viewInvites(){
        const modal = await this.modalController.create({
            component: GroupInvitesPage,
            componentProps:{}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {

                if (result && result.data){     
                    this.groupService.getJoinedGroups().then((data:Array<any>) => {
                        this.groups = data;

                        for (let group of this.groups){
                            if (group.id === result.group_id){
                                this.selectedGroup = group;
                                break;
                            }
                        }

                    })              
                }
            });        
        
        
        
        await modal.present();    
    }    
    
    public async leaveGroup(groupId){
        const alert = await this.alertController.create({
          header: 'Leave Group',
          message: 'Are you sure?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                    this.loading = true;
                    this.selectedGroup = {};
                    this.groupService.leaveGroup(groupId).then(() => {
                        this.getGroups();
                    })
              }
            }
          ]
        });

        await alert.present();         
    }     
    
    public async editGroup(group){
        
        if (group.user_id !== this.user.id){
            return;
        }
        
        let updateGroup = {};
        Object.assign(updateGroup, group);
        
        const modal = await this.modalController.create({
            component: EditGroupPage,
            componentProps:{group:updateGroup}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {

 
                if (result && result.data){     
                    this.groupService.editGroup(result.data.group).then(() => {
                        Object.assign(group, result.data.group);
                    });
                }  
            });        
        
        
        
        await modal.present();        
        
        
    }    
    
    
    
    public async openProfile(userId){
        const modal = await this.modalController.create({
          component: ProfilePage,
          componentProps: {profile:userId}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data){           
                } 
            });        
        
        
        
        await modal.present();          
    }
    
    
    public selectLayer(layer){
        
        this.modalController.dismiss({selectedLayer:layer});
        
    }
    
        
    
    
    public dismiss(){
        this.modalController.dismiss();
    }    
    
      

}
