import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';
import {GroupService} from '../../services/group/group.service';
import {LayerService} from '../../services/layer/layer.service';
import {MapService} from '../../services/map/map.service';
import {ViewTrackPage} from '../view-track/view-track.page';
import {ViewPlacemarkPage} from '../view-placemark/view-placemark.page';

@Component({
  selector: 'app-search',
  templateUrl: './search.page.html',
  styleUrls: ['./search.page.scss'],
})
export class SearchPage implements OnInit {

    
    public search:string;
    public searchResults:Array<any>;
    
    public defaultLayers:Array<any>;
    public privateLayers:Array<any>;
    public groupLayers:any;
    
    
    
    public groups:Array<any>;
    
    public loading:boolean;
    
    
    constructor(public modalController: ModalController,private navParams: NavParams, private mapService:MapService, private groupService:GroupService, private layerService:LayerService) { 

        let search = this.navParams.get('search');
        console.log(search)
        this.search = search ? search : "";  
        
        this.loading = true;
        
        this.searchResults = [] ;  
        
        
        this.defaultLayers = this.navParams.get('default_layers') ? this.navParams.get('default_layers') : [];
        this.privateLayers = this.navParams.get('private_layers') ? this.navParams.get('private_layers') : [];
        
        
        this.groups = [];
        this.groupLayers = {};
        this.groupService.getJoinedGroups().then((data:Array<any>) => {
            this.groups = data;
            
            this.loading = false;
            
            for (let group of this.groups){
                this.getGroupLayers(group.name, group.id);
            }
            
            
        });       
          
        this.doSearch();        
        
        

    }

    ngOnInit() {
    }
    
    
    
    public getGroupLayers(group, groupId){
        this.layerService.getGroupLayers(groupId).then((data) => {
            this.groupLayers[group] = data;
            this.doSearch();
        });        
    } 
    
    
    public doSearch(){
        this.searchResults = []  
        let search = this.search ? this.search.toLowerCase() : "";
       
        
        let defaultResults = this.getLayerResults(this.defaultLayers, search);

        
        if (defaultResults.length > 0){
            this.searchResults.push({
                group: {name: "Get Outdoors"},
                results: defaultResults
            })
        }
        
        let privateResults = this.getLayerResults(this.privateLayers, search);

        
        if (privateResults.length > 0){
            this.searchResults.push({
                group: {name: "My Map"},
                results: privateResults
            })
        }  
        
        
        
        for (let group of this.groups){
            
            if (this.groupLayers[group.name]){
                let groupResults = this.getLayerResults(this.groupLayers[group.name], search);


                if (groupResults.length > 0){
                    this.searchResults.push({
                        group: {name: group.name},
                        results: groupResults
                    })
                }    
            }         
        }
        
        
        
                
    }  
    
    
    
    private getLayerResults(layers, search){
        let results = [];
        
        for (let layer of layers){
            for (let track of layer.tracks){
                let name = track.name.toLowerCase();
                if (!this.search || name.indexOf(search) > -1){
                    track["layer"] = layer;
                    results.push(track);
                }
            }
            for (let placemark of layer.placemarks){
                let name = placemark.name.toLowerCase();
                if (!this.search || name.indexOf(search) > -1){
                    placemark["layer"] = layer;
                    results.push(placemark);
                }                
            }
        }
        
        return results;           
    } 
    
    
    
    public viewItem(item){
        if (item.point_type){
            this.viewPlacemark(item);           
            this.mapService.moveMaps(item.latitude, item.longitude);
  
        }
        else if (item.id){
            this.viewTrack(item);
            this.mapService.moveMaps(item.points[0].latitude, item.points[0].longitude);
        }
    }
    
    
   public async viewTrack(track){      
        const modal = await this.modalController.create({
          component: ViewTrackPage,
          componentProps: {track:track}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data){            

                } 
            });        
        
        
        
        await modal.present();          
    }     
    
    
   public async viewPlacemark(placemark){
        const modal = await this.modalController.create({
          component: ViewPlacemarkPage,
          componentProps: {placemark:placemark}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data){            

                } 
            });        
        
        
        
        await modal.present();              
    }  
    
    
    
    public toggleTrackVisibility(track, ev){
        ev.preventDefault();
        ev.stopPropagation();
        
        track.visible = !track.visible
        
        
        if (track.visible){
            console.log("adding");
            console.log(track);
            this.mapService.showTrack(track);
            
            
            

        }
        else{
            this.mapService.hideTrack(track);
        }
        
        
    }     
    
    
    public togglePlacemarkVisibility(placemark, ev){
        ev.preventDefault();
        ev.stopPropagation();
        
        placemark.visible = !placemark.visible
        
        
        if (placemark.visible){
            console.log("adding");
            console.log(placemark);
            this.mapService.showPlacemark(placemark);
            
            
            

        }
        else{
            this.mapService.hidePlacemark(placemark);
        }
        
        
    }        
    
    
    
    
    
    
    public dismiss(){
        this.modalController.dismiss();
    }

}
