import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';


@Component({
  selector: 'app-edit-layer',
  templateUrl: './edit-layer.page.html',
  styleUrls: ['./edit-layer.page.scss'],
})
export class EditLayerPage implements OnInit {
    
    public layer:any;
    constructor(public modalController: ModalController,private navParams: NavParams) { 
    
            this.layer = {};
            let layer = this.navParams.get('layer');
            this.layer = Object.assign({}, layer );       
    }

    ngOnInit() {
    }
    
    public dismiss(){
        this.modalController.dismiss();
    } 
    
    
    public update(){
        this.modalController.dismiss({layer:this.layer});
    }        

}
