import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { CreateGroupPage } from '../create-group/create-group.page';
import {GroupService} from '../../services/group/group.service';

@Component({
  selector: 'app-join-group',
  templateUrl: './join-group.page.html',
  styleUrls: ['./join-group.page.scss'],
})
export class JoinGroupPage implements OnInit {
    
    
    public search:string;
    public groups:any;
    public page:number;
    public loading:boolean;
    
    constructor(public loadingController: LoadingController, public alertController: AlertController,
        public modalController: ModalController, private navController:NavController,
        private authenticationService:AuthenticationService,private navParams: NavParams, private groupService:GroupService) { 
    
          
        this.groups = {data:[], count:0};        
        
        this.loading = true;
        this.search = "";
        this.page = 1;
        
        this.getGroups();    
    
    
    }

    ngOnInit() {
    }
    
    public getGroups(){
        this.groupService.getGroups(this.search, this.page).then((data) => {
            this.groups = data;
            this.loading = false;
        }).catch(() => {
            this.loading = false;
        }) 
    }
    
    
    public loadMoreGroups(){
        this.page += 1;
        this.groupService.getGroups(this.search, this.page).then((data:any) => {
            this.groups.data.concat(data.data);
        })        
    }
    
    public doSearch(){
        this.page = 1;
        this.getGroups();
    }
    
    
    public async createGroup(){
 
        
        const modal = await this.modalController.create({
            component: CreateGroupPage,
            componentProps:{}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {


                if (result && result.data){     
                    this.groupService.createGroup(result.data.group).then((data:any) => {
                        this.join(data.id);
                    })
                }  
            });        
        
        
        
        await modal.present();          
        
               
    }    
    
    
    public joinGroup(group){
    
        this.groupService.joinGroup(group.id).then(() => {
            this.join(group.id);
        })       
    }       
    
    public join(groupId){ 
        this.modalController.dismiss({group_id:groupId});
    }    
    
    public dismiss(){ 
        this.modalController.dismiss();
    } 
}
