import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, throwError} from 'rxjs';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class BillingService {

    constructor(private http: HttpClient, public storage: Storage) { }
    
    
  
    
    public upgrade(stripeId:string) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/subscriptions/start?token=' + token, {id:stripeId}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
    }      
    
    public cancel() {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/subscriptions/cancel?token=' + token, {}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });  
    }    
    
    public getInvoices() {
        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/invoices?token=' + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
    }      
    public getInvoice(id) {
        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/invoices/' + id + '?token=' + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });        
        
        
    }      
    
    
}
