import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';

import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import {FileTransfer, FileUploadOptions, FileTransferObject} from '@ionic-native/file-transfer/ngx/';

import {environment} from '../../../environments/environment';

import {AuthenticationService} from '../../services/authentication/authentication.service';
import {LayerService} from '../../services/layer/layer.service';
import {GroupService} from '../../services/group/group.service';


@Component({
  selector: 'app-create-placemark',
  templateUrl: './create-placemark.page.html',
  styleUrls: ['./create-placemark.page.scss'],
})
export class CreatePlacemarkPage implements OnInit {
    
    
    public placemark:any;
    public newPlacemark:any;
    public layers:Array<any>;
    public user:any;
    public layerid:any;
    
    constructor(private router: Router,private transfer: FileTransfer, 
          private camera: Camera, public loadingController: LoadingController, public alertController: AlertController,
          public modalController: ModalController, private navController:NavController,
          private authenticationService:AuthenticationService,private navParams: NavParams, private groupService:GroupService, private layerService:LayerService) {
          
          
            this.placemark = {layer_id:1};  
            
            this.newPlacemark = {};
          
            this.layers = [];
    
             
            
            this.layerid = "";
            
            this.user = {};
            
            
            this.groupService.getCurrentGroup().then((data:any) => {

                let selectedGroup = data;   

                if (selectedGroup.id){
                    this.layerService.getGroupLayers(selectedGroup.id).then((layers:Array<any>) => {
                        this.layers = layers;
                        this.getAdminLayers();
                    })
                }
                else{
                    this.layerService.getPrivateLayers().then((layers:Array<any>) => {
                        this.layers = layers;
                        this.getAdminLayers();
                    })                
                }


            });
            
            console.log(this.placemark);             
                  
          
          
    }
        

    ngOnInit() {
    }
    
    private getAdminLayers(){
        this.authenticationService.getUserData().toPromise().then((data) => {
            this.user = data;
            if (this.user.permission === "admin"){
                this.layerService.getDefaultLayers().then((layers) => {
                    this.layers = this.layers.concat(layers);
                })
            }
            
        });         
    }    
    
    public changeImage(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
                let loading;
                this.loadingController.create({
                  message: 'Your image is being uploaded. Please wait...',
                  duration: 20000
                }).then((el) => {
                    loading = el;
                    el.present();
                });           

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"media_file",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
             
                
                fileTransfer.upload(image, encodeURI(environment.apiUrl + "/upload"), options).then((data) => {

                    if (data.response){
                        let response = JSON.parse(data.response);
                        this.newPlacemark.image = response.file;  
                    }     
                    loading.dismiss();           
                }, (err) => {
                    this.alertController.create({
                        header: "Error",
                        subHeader: "There was a problem uploading your image",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    }).then((el) => {
                        el.present(); 
                    });
                    
                    loading.dismiss();
                                       
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   
    }     
    

    
    public dismiss(){
        this.modalController.dismiss();
    } 
    
    
    public add(){
        this.modalController.dismiss({placemark:this.newPlacemark});
    }  
    
    

}

