import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../services/authentication/authentication.service';
import { ProfileService } from '../../services/profile/profile.service';
import { ModalController, NavParams} from '@ionic/angular';
import { EditProfilePage } from '../../pages/edit-profile/edit-profile.page';
import { SettingsPage } from '../../pages/settings/settings.page';
import * as moment from 'moment';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.page.html',
    styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {

    public user:any;
    public profile:any;
    public loading:boolean; 
    
    constructor(private authenticationService:AuthenticationService, private modalController: ModalController,private navParams: NavParams,
        private profileService:ProfileService) { 
        
        this.loading = true;
        
        this.user = {profile:{}};
        
        let userId = this.navParams.get('profile');
           
        this.profile = {};
        
        this.authenticationService.getUserData().subscribe((value) => {
            this.user = value;
            console.log(this.user);
            if (this.user.id === userId){
                //this.profile = this.user.profile;
                
                //this.loading = false;
                console.log(this.profile);
            }
            this.getProfile(userId);
        });        

    }

    ngOnInit() {
    }
    
    public getProfile(userId){
        this.profileService.getProfile(userId).then((data) => {
            this.profile = data;
                this.loading = false;
        }).catch(() => {
        })
        
        
    }    
    

    
    public updateProfile(){
        this.authenticationService.updateProfile(this.user.profile).then(() => {
            this.authenticationService.retreiveUserData().then(() => {});
        })
    }
    
    public async editProfile(){
        
        if (this.user.id !== this.profile.user_id)
        
        this.user.profile.email = this.user.email;
        const modal = await this.modalController.create({
            component: EditProfilePage,
            componentProps:{
                profile:Object.assign({},this.user.profile)
            }
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    Object.assign(this.user.profile, data.data);
                    this.user.email = this.user.profile.email;
                    this.updateProfile();
                } 
            });        
        
        
        
        await modal.present();           
    }     
    
    
    public async editSettings(){
        const modal = await this.modalController.create({
            component: SettingsPage,
            componentProps:{}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                } 
            });        
        
        
        
        await modal.present();           
    }     
    
    public dismiss(){
        this.modalController.dismiss();
    } 
    
    
    
    public formatDate(date){
        return moment(date).format("DD/MM/YYYY");
    }    
    
    
}
