import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController, Platform } from '@ionic/angular';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {LayerService} from '../../services/layer/layer.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { BillingPage } from '../../pages/billing/billing.page';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage implements OnInit {

    public user:any;
    
    constructor(public plt: Platform, 
        private authenticationService: AuthenticationService, private alertCtrl: AlertController, private iab: InAppBrowser, 
        private modalController: ModalController, private layerService:LayerService, private loadingController:LoadingController) {
        
        this.user = {};
        this.authenticationService.getUserData().subscribe((value) => {
            this.user = value;
        });
    }
    
    ngOnInit() {
    }  
    public openRate(){
        
        if (this.plt.is("ios")){
            this.iab.create("https://apps.apple.com/us/app/", '_system');
        }
        else{
            this.iab.create("https://play.google.com/store/apps/details?id=com.myoutdoors.app", '_system');
        }
        
        
    }
    public openFollow(){
        let link = 'https://www.facebook.com/Getoutdoorsmapsnz';
        this.iab.create(link, '_system');        
    }
 
    
    public openEmail(){

        
        let link = 'mailto:' + "hello@my-outdoors.co.nz";
        this.iab.create(link, '_system');              
        
        
    }
    
    public syncOffline(){
        this.layerService.syncOfflineData();
        
        
        let alert = this.alertCtrl.create({
            header: 'Sync Started',
            message: 'Your offline data is syncing in the background. You can continue to use the app while the sync is in progress.',
            buttons: [
                {
                    text: 'Dismiss',
                    role: 'cancel',
                    handler: data => {
                    }
                }                            
            ]            
        }).then((el) => {
                el.present();
            });               
        
        
        
    }
    
    public async openBilling(){
        const modal = await this.modalController.create({
            component: BillingPage,
            componentProps:{}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                } 
            });        
        
        
        
        await modal.present();           
    }

    
    public openChangePassword(){
        let alert = this.alertCtrl.create({
            header: 'Change Password',
            inputs: [           
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                },
                {
                    name: 'confirmPassword',
                    placeholder: 'Confirm Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.password && data.confirmPassword && data.password === data.confirmPassword){

                            
                            this.authenticationService.changePassword(this.user.email, data.password, data.confirmPassword).then(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Success',
                                    message: 'Your password has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }).then((el) => {
                                    el.present();
                                });                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Error',
                                    message: 'There was an error while changing your password.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangePassword()},200);
                                            }
                                        }                               
                                    ]            
                                }).then((el) => {
                                    el.present();
                                });                                
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                header: 'Error',
                                message: 'Passwords do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangePassword()},200);
                                        }
                                    }                               
                                ]            
                            }).then((el) => {
                                    el.present();
                                });                              
                        }
    
                    }
                }
                
            ]
        }).then((el) => {
            el.present();
        });          
    }
    
    public logout(){
        this.authenticationService.logout();
      
    }  
    
     
    public dismiss(){
        this.modalController.dismiss();
    }    
    
        
}