import { Component, OnInit } from '@angular/core';
import { LoadingController,AlertController,ModalController,NavController,NavParams, ToastController } from '@ionic/angular';
import * as moment from 'moment';
import {BillingService} from '../../services/billing/billing.service';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import {environment} from '../../../environments/environment';
import { Storage } from '@ionic/storage';

@Component({
  selector: 'app-billing',
  templateUrl: './billing.page.html',
  styleUrls: ['./billing.page.scss'],
})
export class BillingPage implements OnInit {
    
    
    
    public invoices:Array<any>;
    public search:string;
    public loading:boolean;
    public cancelLoading:boolean;
    public user:any;
    
    constructor(private billingService:BillingService, public modalController: ModalController, private alertController: AlertController,
      private authenticationService:AuthenticationService, private iab: InAppBrowser, public toastController:ToastController, private storage:Storage) { 
      
        
        this.user = {};
        
        this.authenticationService.getUserData().subscribe(value => {
            
            this.user = value;
            
        });  
        
        
      this.loading = true;
      
      this.invoices = [];
      
      this.getInvoices();      
      
      
      
      }

        ngOnInit() {
        }

    
    public getInvoices(){
        this.billingService.getInvoices().then((data:Array<any>) => {
            this.loading = false;
            this.invoices = data;
        })
    }

    
    public downloadInvoice(invoiceId){        
        this.storage.get("go_token").then((token) => {            
            if (token){  
                this.iab.create(environment.apiUrl + '/invoices/' + invoiceId + '?token=' + token, '_system');
            }
        });
    }    
    
    public async cancelSubscription(){
 
        
        
        const alert = await this.alertController.create({
          header: 'Cancel Subscription',
          message: 'Are you sure?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                    this.cancelLoading = true;
                    this.billingService.cancel().then(() => { 
                        this.showToast('Subscription Cancelled'); 
                        this.getInvoices();  
                        this.authenticationService.getUserData().toPromise().then((data) => {
                            this.user = data;

                        });     

                    }).catch((e) => {
                        this.cancelLoading = false;
                        this.showToast('Failed To Cancel Subscription'); 
                    }); 
              }
            }
          ]
        });

        await alert.present();         
        
                
        
        
    }
    
    private async showToast(text){
        const toast = await this.toastController.create({
          message: text,
          duration: 4000
        });
        toast.present();            
    }       
    
    public formatDate(date){
        return moment(date).format("dddd, MMMM Do YYYY");
    }

    public formatTime(date){
        return moment(date).format("h:mma");
    }
       
    public dismiss(){
        this.modalController.dismiss();
    } 


}
