import { Component, OnInit } from '@angular/core';
import { LoadingController,AlertController,ModalController,NavController,NavParams } from '@ionic/angular';
import {CreateLogPage} from '../create-log/create-log.page';
import {EditLogPage} from '../edit-log/edit-log.page';
import {LogService} from '../../services/log/log.service';
import * as moment from 'moment';

@Component({
  selector: 'app-logs',
  templateUrl: './logs.page.html',
  styleUrls: ['./logs.page.scss'],
})
export class LogsPage implements OnInit {
    
    public logs:any;
    public dates:Array<any>;
    public search:string;
    public loading:boolean;
    
    constructor(public modalController: ModalController, private logService:LogService, private alertController:AlertController) { 
      
      this.search = "";
      this.loading = true;
      
      this.logs = {}
      this.dates = [];
      
      this.getLogs();    
    
    
    
    }

    ngOnInit() {
    }
    
    public getLogs(){
        this.logService.getLogs(this.search).then((data) => {
            this.formatLogs(data);
            this.loading = false;
        }).catch(() => {
            this.loading = false;
        })        
    }
    

    
    public async createLog(){

        
        const modal = await this.modalController.create({
          component: CreateLogPage,
          componentProps: {}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {
            if (result && result.data){    
                console.log(result);
                this.logService.createLog(result.data.log).then((data) => {
                    this.getLogs();
                })
            }    
            });        
        
        
        
        await modal.present();         
        
        
        
              
    }     
    
    
    public async editLog(log){
        
        let updateLog = {};
        Object.assign(updateLog, log);
 
        
        const modal = await this.modalController.create({
          component: EditLogPage,
          componentProps: {log:updateLog}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {
                if (result && result.data){ 
                    
                    Object.assign(log, result.data.log);

                    this.logService.editLog(result.data.log).then((data) => {
                    })                    
                             
                } 
            });        
        
        
        
        await modal.present();         
        
        
             
    }   
    
    
    
    public async deleteLog(logId){
        
        const alert = await this.alertController.create({
          header: 'Delete Log Entry',
          message: 'Are you sure?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                    this.logService.deleteLog(logId).then(() => {
                        this.getLogs();
                    });
              }
            }
          ]
        });

        await alert.present();      
        
    }         
    
    
    private formatLogs(logs){
        
        let dates = [];
        let formattedLogs = {};
        
        for (let log of logs){
            
            let logDate = moment(log.created_at).format("YYYY-MM-DD");
            
            if (dates.indexOf(logDate) < 0){
                dates.push(logDate);
                formattedLogs[logDate] = [log];
            }
            else{
                formattedLogs[logDate].push(log);
            }
            
        }
        
        
        this.dates = dates;
        this.logs = formattedLogs;
        
        
    }
    
    
    
    public formatDate(date){
        return moment(date).format("dddd, MMMM Do YYYY");
    }

    public formatTime(date){
        return moment(date).format("h:mma");
    }
       
    public dismiss(){
        this.modalController.dismiss();
    } 
}
