import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams, AlertController, ToastController } from '@ionic/angular';
import * as moment from 'moment';
import {ProfilePage} from '../profile/profile.page';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { EditTrackPage } from '../edit-track/edit-track.page';
import {LogService} from '../../services/log/log.service';

@Component({
  selector: 'app-view-track',
  templateUrl: './view-track.page.html',
  styleUrls: ['./view-track.page.scss'],
})
export class ViewTrackPage implements OnInit {
    
    public track:any;
    public user:any;
    public gallery:Array<any>;
    constructor(public modalController: ModalController,private navParams: NavParams, public authenticationService: AuthenticationService,
        public logService: LogService, public alertController: AlertController, public toastController:ToastController) { 

        let track = this.navParams.get('track');
        this.track = track ? track : {};  
        
         this.gallery = this.track.gallery ? JSON.parse(this.track.gallery) : [];
        
        
        
        this.user = {};
        this.authenticationService.getUserData().subscribe(value => {
            
            this.user = value;
            
        });          
        
        

    }

    ngOnInit() {
    }

   
    public async openProfile(userId){
        const modal = await this.modalController.create({
          component: ProfilePage,
          componentProps: {profile:userId}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data){           
                } 
            });        
        
        
        
        await modal.present(); 
    }   
       
    
    
    public formatFromNow(date){
        return moment(date).from(moment());
    } 
    
    
    
    
    
   public async editTrack(){
       
       let updateTrack = {};
       
       Object.assign(updateTrack, this.track);
  
        
        const modal = await this.modalController.create({
          component: EditTrackPage,
          componentProps: {track:updateTrack}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {
                
                if (result && result.data){    
                    let updatedTrack = result.data.track;
                    updatedTrack.edited = true; 

                    setTimeout(() => {
                        this.modalController.dismiss({track:updatedTrack});
                    },500);                    
                    
                           
                } 
            });        
        
        
        
        await modal.present();         
        
        
          
    } 
    
    public update(track){

    }
    
    
    public async deleteTrack(){

        
        
        const alert = await this.alertController.create({
          header: 'Delete ' + this.track.name,
          message: 'Are you sure?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                  this.track.deleted = true;
                  setTimeout(() => { this.modalController.dismiss({track:this.track}); },500);
              }
            }
          ]
        });

        await alert.present();           
        
            
      
    }    
        
    
    
     
    public async planTrip(){

        
        
        const alert = await this.alertController.create({
          header: 'Plan Trip',
          message: 'Are you sure? This will add an entry to your log.',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                    this.logService.createLog({title: "Planned Trip", description: "Trip was planned for " + this.track.name}).then((data) => {

                        this.showPlanTripToast();
                    
                        
                        
                    })
              }
            }
          ]
        });

        await alert.present();         
        
        
               
    }   
    
    
    private async showPlanTripToast(){
        const toast = await this.toastController.create({
          message: 'Trip added to your log!',
          duration: 4000
        });
        toast.present();            
    }     
    
    public dismiss(){
        this.modalController.dismiss();
    }  
}
