import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LogService {

    constructor(private http: HttpClient, public storage: Storage) { }
    
    
    
    public getLogs(search) {
        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/logs?token=' + token + "&search=" + search).subscribe((res) => {
                        this.storage.set("go_logs", res);
                        resolve(res);
                    }, (e) => {
                        this.storage.get("go_logs").then((data) => {
                            if (data){
         
                                resolve(data);
                            }
                            else{
                                reject(e);
                            }
                        });
                        
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
    }     
    
    public createLog(log) {
        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/logs?token=' + token, log).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });           
        
        
    }    
    
    public editLog(log) {

        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + '/logs/' + log.id + '?token=' + token, log).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });         
        
        
    }     

    public deleteLog(logId) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.delete(environment.apiUrl + '/logs/' + logId + '?token=' + token).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });           
        
    }     
        
    
    
}
