import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams, AlertController, ToastController } from '@ionic/angular';
import * as moment from 'moment';
import {ProfilePage} from '../profile/profile.page';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { EditPlacemarkPage } from '../edit-placemark/edit-placemark.page';
import {LogService} from '../../services/log/log.service';

@Component({
  selector: 'app-view-placemark',
  templateUrl: './view-placemark.page.html',
  styleUrls: ['./view-placemark.page.scss'],
})
export class ViewPlacemarkPage implements OnInit {

    
    public placemark:any;
    public user:any;
    public gallery:Array<any>;
    constructor(public modalController: ModalController,private navParams: NavParams, public authenticationService: AuthenticationService,
        public logService: LogService, public alertController: AlertController, public toastController:ToastController) { 

        let placemark = this.navParams.get('placemark');
        this.placemark = placemark ? placemark : {};  
        
         this.gallery = this.placemark.gallery ? JSON.parse(this.placemark.gallery) : [];
        
        
        
        this.user = {};
        this.authenticationService.getUserData().subscribe(value => {
            
            this.user = value;
            
        });          
        
        

    }

    ngOnInit() {
    }

   
    public async openProfile(userId){
        const modal = await this.modalController.create({
          component: ProfilePage,
          componentProps: {profile:userId}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data){           
                } 
            });        
        
        
        
        await modal.present(); 
    }   
       
    
    
    public formatFromNow(date){
        return moment(date).from(moment());
    } 
    
    
    
    
    
   public async editPlacemark(){
       
       let updatePlacemark = {};
       
       Object.assign(updatePlacemark, this.placemark);
  
        
        const modal = await this.modalController.create({
          component: EditPlacemarkPage,
          componentProps: {placemark:updatePlacemark}
        });
        
        modal.onDidDismiss()
            .then((result:any) => {
                if (result && result.data){    
                    let updatedPlacemark = result.data.placemark;
                    updatedPlacemark.edited = true; 

                    setTimeout(() => {
                        this.modalController.dismiss({placemark:updatedPlacemark});
                    },500);                    
                    
                           
                } 
            });        
        
        
        
        await modal.present();         
        
        
          
    } 
    
    
    public async deletePlacemark(){

        
        
        const alert = await this.alertController.create({
          header: 'Delete ' + this.placemark.name,
          message: 'Are you sure?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                  this.placemark.deleted = true;
                  setTimeout(() => { this.modalController.dismiss({placemark:this.placemark}); },500);
              }
            }
          ]
        });

        await alert.present();           
        
            
      
    }    
        
    
    
     
    public async planTrip(){

        
        
        const alert = await this.alertController.create({
          header: 'Plan Trip',
          message: 'Are you sure? This will add an entry to your log.',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
              }
            }, {
              text: 'Yes',
              handler: () => {
                    this.logService.createLog({title: "Planned Trip", description: "Trip was planned for " + this.placemark.name}).then((data) => {

                        this.showPlanTripToast();
                    
                        
                        
                    })
              }
            }
          ]
        });

        await alert.present();         
        
        
               
    }   
    
    
    private async showPlanTripToast(){
        const toast = await this.toastController.create({
          message: 'Trip added to your log!',
          duration: 4000
        });
        toast.present();            
    }     
    
    public dismiss(){
        this.modalController.dismiss();
    }   
}
