import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, throwError} from 'rxjs';
import { Storage } from '@ionic/storage';
import { Platform } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class GroupService {

    
    private currentGroup:BehaviorSubject<any>;  
    
    constructor(private http: HttpClient, public storage: Storage) { 
    
        this.currentGroup = new BehaviorSubject<any>({});
    
    }

    
    public getGroups(search,page) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/groups?token=' + token + '&page=' + page + "&search=" + search).subscribe((res) => {
                        this.storage.set("go_groups", res);
                        resolve(res);
                    }, (e) => {
                        this.storage.get("go_groups").then((data) => {
                            if (data){
         
                                resolve(data);
                            }
                            else{
                                reject(e);
                            }
                        });
                        
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
    }  
    
    
    public getJoinedGroups() {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/groups?token=' + token + '&joined=1').subscribe((res) => {
                        this.storage.set("go_joinedgroups", res);
                        resolve(res);
                    }, (e) => {
                        this.storage.get("go_joinedgroups").then((data) => {
                            if (data){
         
                                resolve(data);
                            }
                            else{
                                reject(e);
                            }
                        });
                        
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
        
    }      
    
    public getInvitedGroups() {

        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/groups?token=' + token + '&invited=1').subscribe((res) => {
                        this.storage.set("go_invitedgroups", res);
                        resolve(res);
                    }, (e) => {
                        this.storage.get("go_invitedgroups").then((data) => {
                            if (data){
         
                                resolve(data);
                            }
                            else{
                                reject(e);
                            }
                        });
                        
                    }); 
                }
                else{
                    reject();
                }
            });
        });         
        
        
    }  
    
            
    public getGroup(groupId) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/groups/' + groupId + '?token=' + token).subscribe((res) => {
                        this.storage.set("go_group" + groupId, res);
                        resolve(res);
                    }, (e) => {
                        this.storage.get("go_group" + groupId).then((data) => {
                            if (data){
         
                                resolve(data);
                            }
                            else{
                                reject(e);
                            }
                        });
                        
                    }); 
                }
                else{
                    reject();
                }
            });
        });           
        
        
        
    }     
    
    
    public createGroup(group) {
        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/groups?token=' + token, group).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
        
    }    
    
    public editGroup(group) {

        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.put(environment.apiUrl + '/groups/' + group.id + '?token=' + token, group).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });         
        
        
    }     
     
    
    public joinGroup(groupId) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/joingroup?token=' + token, {group_id:groupId}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
    }    
    
    public inviteUser(user) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/invitegroup?token=' + token, user).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });         
        
        
        
    }    
       
    public leaveGroup(groupId) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/leavegroup?token=' + token, {group_id:groupId}).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
        
    }  
    
    public getMessages(groupId) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.get(environment.apiUrl + '/groupmessages/' + groupId + '?token=' + token).subscribe((res) => {
                        this.storage.set("go_messages" + groupId, res);
                        resolve(res);
                    }, (e) => {
                        this.storage.get("go_messages" + groupId).then((data) => {
                            if (data){
         
                                resolve(data);
                            }
                            else{
                                reject(e);
                            }
                        });
                        
                    }); 
                }
                else{
                    reject();
                }
            });
        });          
        
        
        
    }      
    
    public createMessage(message) {

        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/groupmessages?token=' + token, message).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        reject(e);
                    }); 
                }
                else{
                    reject();
                }
            });
        });         
        
        
    } 
    
    public subscribeCurrentGroup(){
        return this.currentGroup.asObservable();
    }    
    
    public getCurrentGroup() {
        
        return new Promise((resolve, reject) => {
            this.storage.get("go_currentgroup").then((group) => {            
                if (group){            
                    resolve(group);
                }
                else{
                    reject();
                }
            });
        });  
    }

    public setCurrentGroup(group) {
        
        this.storage.set('go_currentgroup', group);
        this.currentGroup.next(group);
    }    
       
    
    
}
