    public syncOfflineData(){
        
        
        this.getLayers().then(() => { //check internet connection
            
            this.getOfflinePlacemarks().then((placemarks:Array<any>) => {
   
                this.storage.set("go_offlineplacemarks", []).then(() => {
                    for (let placemark of placemarks){
                        this.createPlacemark(placemark).then(() => {

                        })
                    }                     
                });
                
                
                         
            }).catch(() => {
                
            })       

            this.getOfflineTracks().then((tracks:Array<any>) => {
                
                this.storage.set("go_offlinetracks", []).then(() => {    
                    for (let track of tracks){
                        this.createTrack(track).then(() => {

                        })
                    }                   
                });                
                

            }).catch(() => {
                
            })  
        
        });      
    }
    
        
    public createPlacemark(point) {

        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/points?token=' + token, point).subscribe((res) => {
                        
                        resolve(res);
                    }, (e) => {
                        
                    
                        this.storage.get("go_offlineplacemarks").then((placemarks) => {
                            
                            
                            
                            if (placemarks){
                                point.id = "offlineplacemark" + placemarks.length;
                                placemarks.push(point);
                         
                            }
                            else{
                                point.id = "offlineplacemark0";
                                placemarks = [point];
   
                            }
                            
                            this.storage.set("go_offlineplacemarks", placemarks).then(() => {
                                resolve(placemarks);
                            });
                            
   
                            
                                   
                            
                            
                            
                        });
                    }); 
                }
                else{
                    reject();
                }
            });
        });            
        
        
    }     
    
    public createTrack(track) {

        
        return new Promise((resolve, reject) => {
            this.storage.get("go_token").then((token) => {            
                if (token){            
                    this.http.post(environment.apiUrl + '/tracks?token=' + token, track).subscribe((res) => {
                        resolve(res);
                    }, (e) => {
                    
                        
                    
                        this.storage.get("go_offlinetracks").then((tracks) => {
                            
                            
                            if (tracks){
                                track.id = "offlinetrack" + tracks.length;
                                tracks.push(track);
                         
                            }
                            else{
                                track.id = "offlinetrack0";
                                tracks = [track];
   
                            }
                            
                            this.storage.set("go_offlinetracks", tracks).then(() => {
                                resolve(tracks);
                            });
                            
                                   
                            
                            
                            
                        });
                    });  
                }
                else{
                    reject();
                }
            });
        });         
        
        
    }     
    
    
