import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController,NavParams, ToastController } from '@ionic/angular';
import {environment} from '../../../environments/environment';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { InviteUserPage } from '../invite-user/invite-user.page';
import {GroupService} from '../../services/group/group.service';


@Component({
  selector: 'app-group-invites',
  templateUrl: './group-invites.page.html',
  styleUrls: ['./group-invites.page.scss'],
})
export class GroupInvitesPage implements OnInit {
    
    
    public groups:any;
    public loading:boolean;
    
    
    constructor(public loadingController: LoadingController, public alertController: AlertController,
        public modalController: ModalController, private navController:NavController,
        private authenticationService:AuthenticationService,private navParams: NavParams, private groupService:GroupService, 
        public toastController:ToastController) { 
        
        this.groups = [];     
        
        this.loading = true;

        
        this.getInvites();        
        
        
        }

    ngOnInit() {
    }
    
    
    
        public getInvites(){
            this.groupService.getInvitedGroups().then((data) => {
                this.groups = data;
                this.loading = false;
            }).catch(() => {
                this.loading = false;
            }) 
        }
        
        
        public joinGroup(group){

            this.groupService.joinGroup(group.id).then(() => {
                this.join(group.id);
            })       
        }       

        public join(groupId){ 
            this.modalController.dismiss({group_id:groupId});
        }   
        
         

        public dismiss(){ 
            this.modalController.dismiss();
        }  
        
        
        public async inviteUser(){

            const modal = await this.modalController.create({
                component: InviteUserPage,
                componentProps:{}
            });

            modal.onDidDismiss()
                .then((result:any) => {


                    if (result && result.data){     
                        this.groupService.inviteUser(result.data.user).then(() => {
              
                            this.showToast('Invitation sent!')                
                        })
                    }  
                });        



            await modal.present();          
        
        
                
        }    
    
    private async showToast(text){
        const toast = await this.toastController.create({
          message: text,
          duration: 2000
        });
        toast.present();            
    }  
}
